#SliderInput.modal-dialog{
  width:400px ;
  top: 240px ;
  right: 70px ;
}

.tool.snapgrid-1 .form_body:before
{    content: '';
    width: 0;
    height: 0;
    position: absolute;
    right: -11px;
    top: 50%;
    border-left: 11px solid #a2a2a2;
    border-top: 11px solid transparent;
    border-bottom: 11px solid transparent;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    z-index: 9;
}
.tool.snapgrid-1 .modal-body.form_body:after 
{
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    right: -10px;
    top: 50%;
    border-left: 10px solid #ffffff;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    z-index: 99;
}
#SliderModalBody.modal-body {
    position: relative;
    padding: 20px 0px 25px 5px;
}
